* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #f4e4ba;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.container {
  cursor: pointer;
  background-color: #fff;
  border-radius: 1rem;
  width: 40vh;
  min-height: 45vh;
  padding: 2rem;
  box-shadow: 0 4px 8px #e563ba;
}

.container:hover {
  box-shadow: 0 6px 12px #000;
}

form {
  margin: 2rem 0 1rem;
}

form select, button, input {
  border: none;
  border-radius: .75rem;
  outline: none;
  width: 100%;
}

form input {
  border: 1px solid #d3d3d3;
  height: 3rem;
  margin-top: .5rem;
  padding: .5rem;
  font-size: 1rem;
}

form input:hover {
  background-color: #d3d3d3;
}

.dropdown {
  justify-content: space-between;
  align-items: center;
  margin-top: 2rem;
  display: flex;
}

.dropdown i {
  margin-top: 1rem;
  font-size: 1.5rem;
}

.select-container img {
  border-radius: .5rem;
  max-width: 2rem;
  margin-left: .5rem;
  margin-right: .5rem;
}

.select-container {
  cursor: pointer;
  border: 1px solid #d3d3d3;
  border-radius: .5rem;
  justify-content: center;
  align-items: center;
  width: 6rem;
  display: flex;
}

.select-container select {
  font-size: .75rem;
}

.msg {
  margin: 2rem 0;
}

form button {
  color: #fff;
  cursor: pointer;
  background-color: #af4d98;
  height: 3rem;
  font-size: 1rem;
}
